.stars {
  display: flex;

  &__star {
    height: 16px;
    width: 16px;
    margin-right: 4px;
    background-image: url("../images/star-active.svg");
    background-repeat: no-repeat;
    background-position: center;
  }

  &__star:last-child {
    background-image: url("../images/star.svg");
  }
}
